'use client';

import { useEffect, useState, useRef } from 'react';
import Layout from '@/components/Layout';
import { documentsApi } from '@/lib/api';
import toast from 'react-hot-toast';
import { DocumentArrowUpIcon, MagnifyingGlassIcon, XMarkIcon } from '@heroicons/react/24/outline';

interface Document {
  id: number;
  filename: string;
  document_type: string;
  extracted_data: any;
  created_at: string;
}

export default function DocumentsPage() {
  const [documents, setDocuments] = useState<Document[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedDoc, setSelectedDoc] = useState<Document | null>(null);
  const [uploading, setUploading] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [docTypeFilter, setDocTypeFilter] = useState<string>('all');
  const fileInputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    fetchDocuments();
  }, [docTypeFilter]);

  const fetchDocuments = async () => {
    setLoading(true);
    try {
      const data = await documentsApi.getAll(0, 100, docTypeFilter === 'all' ? undefined : docTypeFilter);
      // Handle both array and object with documents property
      const docs = Array.isArray(data) ? data : (data?.documents || []);
      setDocuments(docs);
    } catch (error: any) {
      console.error('Failed to load documents:', error);
      setDocuments([]);
      if (error.response?.status !== 404) {
        toast.error(error.response?.data?.detail || 'Failed to load documents');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleFileUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    setUploading(true);
    try {
      const result = await documentsApi.upload(file);
      toast.success(`Document processed! Created ${result.transactions_created || 0} transactions.`);
      await fetchDocuments();
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    } catch (error: any) {
      toast.error(error.response?.data?.detail || 'Failed to upload document');
      console.error(error);
    } finally {
      setUploading(false);
    }
  };

  const handleUpdateDocument = async (docId: number, field: string, value: any) => {
    try {
      await documentsApi.update(docId, {
        extracted_data: { [field]: value },
      });
      toast.success('Document updated successfully');
      await fetchDocuments();
      if (selectedDoc?.id === docId) {
        const updated = documents.find((d) => d.id === docId);
        if (updated) setSelectedDoc(updated);
      }
    } catch (error) {
      toast.error('Failed to update document');
    }
  };

  const filteredDocuments = documents.filter((doc) => {
    const matchesSearch = doc.filename.toLowerCase().includes(searchTerm.toLowerCase()) ||
      (doc.extracted_data?.vendor || '').toLowerCase().includes(searchTerm.toLowerCase());
    return matchesSearch;
  });

  const docTypes = Array.from(new Set(documents.map((d) => d.document_type).filter(Boolean)));

  return (
    <Layout>
      <div className="space-y-8">
        {/* Header */}
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-4xl font-bold bg-gradient-to-r from-indigo-600 to-purple-600 bg-clip-text text-transparent">
              Documents
            </h1>
            <p className="text-slate-600 mt-2">Manage and process your documents</p>
          </div>
          <div>
            <input
              type="file"
              ref={fileInputRef}
              onChange={handleFileUpload}
              accept=".pdf,.png,.jpg,.jpeg"
              className="hidden"
              id="file-upload"
              disabled={uploading}
            />
            <label
              htmlFor="file-upload"
              className={`inline-flex items-center px-6 py-3 border border-transparent text-sm font-semibold rounded-xl shadow-lg text-white bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-700 hover:to-purple-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 cursor-pointer transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed ${
                uploading ? 'opacity-50 cursor-not-allowed' : ''
              }`}
            >
              {uploading ? (
                <>
                  <svg className="animate-spin -ml-1 mr-2 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                  Uploading...
                </>
              ) : (
                <>
                  <DocumentArrowUpIcon className="h-5 w-5 mr-2" />
                  Upload Document
                </>
              )}
            </label>
          </div>
        </div>

        {/* Filters */}
        <div className="bg-white/80 backdrop-blur-xl rounded-2xl shadow-lg p-6 border border-slate-200/60">
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            <div>
              <label className="block text-sm font-semibold text-slate-700 mb-2">Search</label>
              <div className="relative">
                <MagnifyingGlassIcon className="absolute left-4 top-1/2 transform -translate-y-1/2 h-5 w-5 text-slate-400" />
                <input
                  type="text"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  placeholder="Search by filename or vendor..."
                  className="pl-12 w-full border border-slate-300 rounded-xl px-4 py-3 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all bg-white"
                />
              </div>
            </div>
            <div>
              <label className="block text-sm font-semibold text-slate-700 mb-2">Filter by Type</label>
              <select
                value={docTypeFilter}
                onChange={(e) => setDocTypeFilter(e.target.value)}
                className="w-full border border-slate-300 rounded-xl px-4 py-3 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all bg-white"
              >
                <option value="all">All Types</option>
                {docTypes.map((type) => (
                  <option key={type} value={type}>
                    {type}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>

        {/* Documents List */}
        {loading ? (
          <div className="grid grid-cols-1 gap-4">
            {[1, 2, 3].map((i) => (
              <div key={i} className="bg-white rounded-2xl shadow-lg p-6 h-32 animate-pulse">
                <div className="h-4 bg-slate-200 rounded w-1/3 mb-4"></div>
                <div className="h-4 bg-slate-200 rounded w-1/2"></div>
              </div>
            ))}
          </div>
        ) : filteredDocuments.length === 0 ? (
          <div className="bg-white/80 backdrop-blur-xl rounded-2xl shadow-lg p-16 text-center border border-slate-200/60">
            <div className="mx-auto w-20 h-20 bg-gradient-to-br from-indigo-100 to-purple-100 rounded-full flex items-center justify-center mb-6">
              <DocumentArrowUpIcon className="h-10 w-10 text-indigo-600" />
            </div>
            <h3 className="text-2xl font-bold text-slate-900 mb-2">No documents found</h3>
            <p className="text-slate-600 mb-6">Get started by uploading your first document.</p>
            <label
              htmlFor="file-upload"
              className="inline-flex items-center px-6 py-3 border border-transparent text-sm font-semibold rounded-xl shadow-lg text-white bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-700 hover:to-purple-700 cursor-pointer transition-all duration-200"
            >
              <DocumentArrowUpIcon className="h-5 w-5 mr-2" />
              Upload Your First Document
            </label>
          </div>
        ) : (
          <div className="grid grid-cols-1 gap-4">
            {filteredDocuments.map((doc) => (
              <div
                key={doc.id}
                className="group bg-white/80 backdrop-blur-xl rounded-2xl shadow-lg hover:shadow-2xl transition-all duration-300 cursor-pointer border border-slate-200/60 hover:border-indigo-300 overflow-hidden"
                onClick={() => setSelectedDoc(doc)}
              >
                <div className="p-6">
                  <div className="flex items-center justify-between">
                    <div className="flex-1 min-w-0">
                      <h3 className="text-lg font-bold text-slate-900 mb-2 truncate">{doc.filename}</h3>
                      <div className="flex flex-wrap items-center gap-3 text-sm">
                        <span className="inline-flex items-center px-3 py-1 rounded-full text-xs font-semibold bg-gradient-to-r from-indigo-100 to-purple-100 text-indigo-700">
                          {doc.document_type || 'Unknown'}
                        </span>
                        {doc.extracted_data?.vendor && (
                          <span className="text-slate-600">Vendor: <span className="font-semibold">{doc.extracted_data.vendor}</span></span>
                        )}
                        {doc.extracted_data?.total && (
                          <span className="text-slate-600">Total: <span className="font-semibold text-emerald-600">${doc.extracted_data.total.toLocaleString()}</span></span>
                        )}
                        <span className="text-slate-500">{new Date(doc.created_at).toLocaleDateString()}</span>
                      </div>
                    </div>
                    <button
                      onClick={(e) => {
                        e.stopPropagation();
                        setSelectedDoc(doc);
                      }}
                      className="ml-4 px-4 py-2 text-sm font-semibold text-indigo-600 hover:text-indigo-700 hover:bg-indigo-50 rounded-xl transition-colors"
                    >
                      View Details â†’
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}

        {/* Document Detail Modal */}
        {selectedDoc && (
          <div className="fixed inset-0 z-50 overflow-y-auto">
            <div className="flex items-center justify-center min-h-screen px-4 py-4">
              <div className="fixed inset-0 bg-slate-900/50 backdrop-blur-sm transition-opacity" onClick={() => setSelectedDoc(null)}></div>
              <div className="relative bg-white rounded-2xl shadow-2xl max-w-4xl w-full max-h-[90vh] overflow-y-auto border border-slate-200">
                <div className="sticky top-0 bg-white border-b border-slate-200 px-8 py-6 flex justify-between items-center z-10 backdrop-blur-xl bg-white/95">
                  <h2 className="text-2xl font-bold bg-gradient-to-r from-indigo-600 to-purple-600 bg-clip-text text-transparent">{selectedDoc.filename}</h2>
                  <button
                    onClick={() => setSelectedDoc(null)}
                    className="text-slate-400 hover:text-slate-600 p-2 hover:bg-slate-100 rounded-xl transition-colors"
                    aria-label="Close"
                  >
                    <XMarkIcon className="h-6 w-6" />
                  </button>
                </div>
                <div className="p-8 space-y-6">
                  <div>
                    <h3 className="text-lg font-bold text-slate-900 mb-6">Extracted Data</h3>
                    {selectedDoc.extracted_data ? (
                      <div className="space-y-4">
                        {Object.entries(selectedDoc.extracted_data).map(([key, value]) => (
                          <div key={key} className="border-b border-slate-200 pb-4 last:border-b-0">
                            <label className="block text-sm font-bold text-slate-700 mb-2">
                              {key.replace('_', ' ').replace(/\b\w/g, (l) => l.toUpperCase())}
                            </label>
                            {typeof value === 'object' && value !== null ? (
                              <pre className="text-sm text-slate-600 bg-slate-50 p-4 rounded-xl border border-slate-200 overflow-x-auto">
                                {JSON.stringify(value, null, 2)}
                              </pre>
                            ) : (
                              <input
                                type="text"
                                value={String(value || '')}
                                onChange={(e) => handleUpdateDocument(selectedDoc.id, key, e.target.value)}
                                className="w-full border border-slate-300 rounded-xl px-4 py-3 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                              />
                            )}
                          </div>
                        ))}
                      </div>
                    ) : (
                      <div className="text-center py-12">
                        <p className="text-slate-500">No extracted data available</p>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </Layout>
  );
}
